  .chart-container {
    max-width: 1024px;
    margin: 0 auto;
    text-align: center;
    position: relative;
  }
  
  .chart-container h2 {
    font-size: 24px;
    font-weight: bold;
    color: #1f2937;
    margin-bottom: 16px;
  }

  .tooltip {
    position: absolute;
    background-color: #ffffff;
    border: 1px solid #e5e7eb;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    padding: 8px;
    pointer-events: none;
    transition: opacity 0.2s ease-in-out;
    z-index: 10;
  }
  
  .tooltip.hidden {
    opacity: 0;
  }
  
  .tooltip.visible {
    opacity: 1;
  }